import { motion } from "framer-motion";
import { useState, useEffect } from "react";
import { Download } from "lucide-react";
import { ModernButton } from "./ModernButton";
import { isMobileDevice, prefersReducedMotion } from "../src/utils/deviceDetect";
import { useLanguage } from "../contexts/LanguageContext";
import { getTranslation } from "../translations";

export function MagneticCTA() {
  const { language } = useLanguage();
  const t = getTranslation(language);
  const [isMobile, setIsMobile] = useState(false);
  const [reducedMotion, setReducedMotion] = useState(false);
  const [isSafariBrowser, setIsSafariBrowser] = useState(false);

  useEffect(() => {
    setIsMobile(isMobileDevice());
    setReducedMotion(prefersReducedMotion());
    // Detect Safari
    const ua = navigator.userAgent;
    setIsSafariBrowser(/^((?!chrome|android).)*safari/i.test(ua));
  }, []);

  const animationDuration = isSafariBrowser ? 0 : (isMobile || reducedMotion ? 0.3 : 0.6);

  // Conditional component - use plain HTML on Safari
  const MotionDiv = isSafariBrowser ? 'div' : motion.div;

  return (
    <section className="py-32 relative overflow-hidden">
      {/* Static Background */}
      <div className="absolute inset-0">
        <div className="absolute inset-0 bg-gradient-to-b from-transparent via-cyan-950/20 to-transparent" />
        <div
          className="absolute inset-0 opacity-30"
          style={{
            backgroundImage: `radial-gradient(circle at 50% 50%, #00ccff22 1px, transparent 1px)`,
            backgroundSize: "50px 50px",
          }}
        />
      </div>

      <div className="container mx-auto px-6 relative z-10">
        <div className="max-w-4xl mx-auto text-center">
          {/* Main CTA Card */}
          <div className="relative p-12 md:p-16 rounded-3xl bg-gradient-to-br from-slate-900/90 to-slate-800/90 border border-cyan-400/30 backdrop-blur-xl shadow-2xl">
            {/* Static Gradient Border */}
            <div
              className="absolute inset-0 rounded-3xl"
              style={{
                background: "linear-gradient(135deg, #00ccff, #1e40af)",
                padding: "2px",
                WebkitMask: "linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0)",
                WebkitMaskComposite: "xor",
                maskComposite: "exclude",
              }}
            />

            {/* Content */}
            <div className="relative z-10">
              <MotionDiv
                {...(isSafariBrowser ? {} : {
                  initial: { opacity: 0, y: 20 },
                  whileInView: { opacity: 1, y: 0 },
                  viewport: { once: true },
                  transition: { duration: animationDuration }
                })}
              >
                <h2 className="text-4xl md:text-6xl font-bold mb-6">
                  <span className="bg-gradient-to-r from-white via-cyan-200 to-cyan-400 bg-clip-text text-transparent">
                    {t.cta.transformTitle}
                  </span>
                </h2>
              </MotionDiv>

              {/* Magnetic Download Button - Centered */}
              <MotionDiv
                {...(isSafariBrowser ? {} : {
                  initial: { opacity: 0, scale: 0.9 },
                  whileInView: { opacity: 1, scale: 1 },
                  viewport: { once: true },
                  transition: { duration: animationDuration, delay: isMobile || reducedMotion ? 0.1 : 0.2 }
                })}
                className="flex justify-center items-center"
              >
                <MagneticButton>
                  <a
                    href="#"
                    onClick={(e) => {
                      e.preventDefault();
                      alert(t.download.comingSoon);
                    }}
                  >
                    <ModernButton
                      size="lg"
                      glow
                      icon={<Download className="w-6 h-6" />}
                    >
                      {t.cta.downloadButton}
                    </ModernButton>
                  </a>
                </MagneticButton>
              </MotionDiv>
            </div>

            {/* Decorative Orbs */}
            <div className="absolute top-0 right-0 w-64 h-64 bg-cyan-500/10 rounded-full blur-3xl -z-10" />
            <div className="absolute bottom-0 left-0 w-64 h-64 bg-blue-600/10 rounded-full blur-3xl -z-10" />
          </div>
        </div>
      </div>
    </section>
  );
}

export default MagneticCTA;

function MagneticButton({ children }: { children: React.ReactNode }) {
  // Simplified button without magnetic effect for better performance
  return <div>{children}</div>;
}